.TodoSearch {
    background: #F9FBFC;
    border-radius: 20px;
    border: 0px solid #202329;
    margin: 0 0px;
    height: 64px;
    width: calc(100% );
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    margin-bottom: 40px;
  }
  
  .TodoSearch::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .TodoSearch:focus {
    outline-color: #61DAFA;
  }